body {
  margin: 0;
}

.container_header {
  display: flex;
  flex-direction: row;
  align-items: baseline;
  align-self: flex-start;

  margin: 30px;
}

.title {
  margin-left: 1vh;
  font-size: 6vh;
  font-weight: bold;
  align-self: center;
}


/* configuracoes da logo */
.editImage {
  min-height: 12vh;
  max-height: 15vh;

}
.container_form {
  display: flex;
  flex-direction: column;
}

.container_intern_form {
  display: flex;
  flex-direction: row;
  align-items: center;
}

.input_text {
  width: 50vw;
  min-width: 200px;
  height: 57px;
}

.button {
  height: 57px;
  width: 12vw;
}

.span_error {
  margin: 5px 0 5px 2px;
  color: red;
  font-weight: bold;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 10px;
}



.container_repo {
  display: grid;
  grid-template-columns: 300px 90px 90px 90px 150px;
  grid-column-gap: 20px;

  background-color: #e5e5e5;
  padding: 20px;
  margin-bottom: 20px;

  border-radius: 5px;

}

.image_repo {
  height: 15px;
  margin: 2px;
}
.container_user_data {
  display: flex;
  flex-direction: column;
  align-self: flex-start;
  width: 20vw;

  background-color: #e5e5e5;
  padding: 10px;

  margin: 0 20px 70px 70px;
  border-radius: 5px;
}

.profile_pic {
  width: 25vh;
  margin: 20px;
  align-self: center;
}

.description {
  display: flex;
  flex-direction: column;
  font-family: sans-serif, Arial, Helvetica;
}

span {
  margin-bottom: 5px;
}

.container_explanation {
  margin: 50px 30px 30px 30px;
  display: flex;
  flex-direction: row;
  align-items: center;
}

.pic_1 {
  height: 300px;
}

.intern_container_explanation {
  width: 50vw;
  display: flex;
  flex-direction: column;
}

.span_text {
  margin: 10px;
}

.pic_2 {

  width: 70px;
  margin: 20px;
  align-self: center;
  
}

.Home {
  background-color: #F5F5F5;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;

}

.data {
  margin-top: 30px;
  display: flex;
  flex-direction: row;
}

.data_container {
  display: flex;
  flex-direction: row;
}

.repos_container {
  display: flex;
  flex-direction: column;
}

.created {
  font-size: 10px;
}

.github_icon {
  width: 15px;
  align-self: baseline;
}

